.sliding-panels {
  display: flex;
    display-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none; }
  .sliding-panels.hasTransition .panel {
    transition: 350ms width; }
  .sliding-panels.hasTransition .divider {
    transition: 350ms left; }
